.item{
  width: 350px;
}

.item p{
  margin: 6px 0;
}

.item-price{
  display: flex;
  gap: 20px;
}

.item-price-new{
color: #374151;
font-size: 20px;
font-weight: 500;
}

.item-price-old{
  color: #8c8c8c;
  font-size: 20px;
  font-weight: 500;
  text-decoration: line-through;
}

.item:hover{
  transform: scale(1.05);
transition: .7s;
}